/** UI **/
* {
  margin: 0;
  box-sizing: border-box;
}

header {
  background-color: #d9d9d9;
  /*background-color: rgb(230, 227, 221);*/
  width: 100%;
  height: 90px;
  display: flex;
  padding: 5px;
  position: sticky;
  margin-bottom: 10px;
}

.container-logo {
  width: 40%;
  display: flex;
  align-items: center;
}

.logo:hover {
  color: #ff6d1f;
  transition: 0.3s;
}

.logo {
  text-decoration: none;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.logo .nombre {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 2rem;
  font-style: normal;
  padding-bottom: 5px;
}

.logo .slogan {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 1rem;
  font-style: normal;
}

.container-menu {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-end;
  width: 100%;
  align-items: center;
}

ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
}

ul li {
  list-style: none;
  margin: 0 10px;
  padding: 5px;
}

ul li a {
  color: #ff6d1f;
  text-decoration: none;
}

ul li a p:hover {
  transition: ease-out 0.2s;
  color: #ff6d1f;
  font-size: 1rem;
  font-weight: semi-bold;
}

ul li a p {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 0.9rem;
  font-style: normal;
}

ul li select {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 0.9rem;
  font-style: normal;
  color: black;
  background-color: rgb(230, 227, 221);
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 30px;
}

* {
  margin: 0;
}

footer {
  background-color: #d9d9d9;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 90px;
}

footer div {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 70px;
  margin: 0 10px;
  align-items: center;
}

footer div.cp {
  display: flex;
  flex-direction: column;
  float: left;
