}

footer div.cp div {
  align-items: center;
  justify-content: flex-start;
}

footer img {
  width: 20px;
  height: 20px;
  margin-left: 10px;
}

footer div a {
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 5px;
}

footer .redes {
  display: flex;
  flex-direction: row;
}

.texto-footer {
  font-family: "Montserrat", system-ui;
  font-size: 12px;
  color: white;
}

.direccion {
  font-family: "Montserrat", system-ui;
  font-size: 10px;
  margin-left: 10px;
  color: white;
}

* {
  margin: 0;
}

@media (min-width: 768px) {
  .contenedor-posts {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
