/*styling star rating*/
.container-estrella {
  border-top: 0.5px solid rgb(27, 27, 26);
  padding: 5px;
  margin-top: 5px;
  width: 100%;
}

.rating {
  border: none;
}

.rating-wrap p {
  font-family: "Montserrat", system-ui;
  font-size: 1rem;
  font-style: thin;
  color: rgb(27, 27, 26);
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 20px;
}

.rating > input {
  display: none;
}

.rating > label:before {
  content: "\f005";
  font-family: FontAwesome;
  font-size: 1.5rem;
  display: inline-block;
  cursor: pointer;
  text-align: center;
  margin: 5px;
}

.center .rating {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
}

.rating input {
  display: none;
}

.rating label {
  color: #282525;
  cursor: pointer;
}

.rating label:hover,
.rating label:hover ~ label {
  color: #2ddc76;
}

.rating input:checked ~ label {
  color: #2ddc76;
}

.star-rating {
  direction: row-reverse;
  display: inline-block;
  font-size: 1.5rem; /* Ajusta el tamaño según sea necesario */
}

.star-rating .fa-star,
.star-rating .fa-star-half-o {
  color: #FFD700; /* Color dorado para las estrellas */
}

.star-rating .fa-star {
  color: #ccc; /* Color gris o cualquier otro color para las estrellas vacías */
}

.star-rating .fa-star.checked {
  color: #FFD700; /* Color dorado para las estrellas llenas */
}

